* {
  font-family: "Open Sans", sans-serif;
}

body {
  margin: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

a,
a:focus,
a:active {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

i {
  font-size: 50px;
  margin: 5px;
}

.navLink {
  background-image: linear-gradient(to right, #ffffff, #ffffff 50%, #000 50%);
  background-size: 200% 100%;
  background-position: -100%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: all 0.3s ease-in-out;
  margin-top: 0.4rem;
}

.navLink:before {
  content: "";
  background: #000000;
  display: block;
  position: absolute;
  bottom: -3px;
  left: 0;
  width: 0;
  height: 3px;
}

.navLink:hover {
  background-position: 0;
}

.navLink::before {
  width: 100%;
}

main img {
  height: 20vw;
  width: 23vw;
  border: 0.2rem solid black;
  filter: drop-shadow(3px 5px 5px rgb(0, 0, 0));
  border-radius: 13%;
  margin: 0.3rem;

  max-height: 240px;
  max-width: 275px;
}

main img:hover {
  transform: scale(1.05);
}

.socialLinks:hover {
  transform: scale(1.3);
}

.mainPortfolio {
  background-image: url("./images/portfoliobackground.JPG");
  width: 81%;
  height: 100%;
  display: flex;
  background-size: cover;
  overflow: scroll;
}

.portfolioContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
}

.portfolioOne {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.portfolioTwo {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.portfolioThree {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;

}

.skills {
  width: 100%;
  text-align: center;
  border-bottom: 2px solid black;
  font-size: 1.3rem;
  width: 100%;
  margin-bottom: 0.1rem;
}

.main {
  display: flex;
  justify-content: space-between;
  height: 100%;
}


@media screen and (min-device-width: 0px) and (max-device-width: 425px) { 

  .main {
    flex-direction: column;
  }

  .navHeader {
    width: 100% !important;
  }

  .resumeCon{
    width: 100% !important;
  }

  .aboutCon{
    width: 100% !important;
  }

  .mainPortfolio {
    width: 100%;
  }

}

